import React from 'react'
// import { Link } from 'react-router-dom'
import { ULink } from '../../universal'
import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import { connect } from 'react-redux'
import { UI_SIDEBAR_CLOSE, UI_LOGIN_MODAL_OPEN } from '../../../redux/actionTypes'
import { actionLogout } from '../../../redux/actionCreators/userActions'
import HomeIcon from '@material-ui/icons/Home';
import AddBoxIcon from '@material-ui/icons/AddBox';
// import AccountTreeIcon from '@material-ui/icons/AccountTree';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSignInAlt, faSignOutAlt } from '@fortawesome/free-solid-svg-icons'
import {
    actionSaveTaleAndReset,
} from '../../../redux/actionCreators/createTaleActions'

const useStyles = makeStyles(theme => ({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  listItem: {
    borderBottom: "1px solid " + theme.palette.primary.main
  },
  icoInList: {
    minWidth: 40
  },
  ico: {
    fontSize: 30
  },
  faIco: {
    fontSize: 27,
    color: theme.palette.primary.main
  }
}))

const navs = [
  {
    name: "Home",
    url: "/home",
    icon: HomeIcon
  },
  {
    name: "Create Tale",
    url: "/create-tale",
    icon: AddBoxIcon
  },
  {
    name: "My Tales",
    url: "/my-tales",
    icon: BusinessCenterIcon
  }
]

const Sidebard = props => {
  const classes = useStyles()
  const { openLoginModal, user, doLogout, resetWIPTale } = props

  const beforeYouGo = url => {
    if (url === "/create-tale") {
      // this is the best place to cleanup the current WIP tale
      resetWIPTale()
    }
  }

  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={props.closeSidebar}
      onKeyDown={props.closeSidebar}
    >
      <List>
        {navs.map((nav, index) => (
          <ULink key={nav.name} to={nav.url} className="anchor-default-none">
            <ListItem button className={classes.listItem} onClick={() => beforeYouGo(nav.url)}>
              <ListItemIcon className={classes.icoInList}>
                <nav.icon color="primary" className={classes.ico} />
              </ListItemIcon>
              <ListItemText primary={nav.name} />
            </ListItem>
          </ULink>
        ))}
        {!user.email && <ListItem button className={classes.listItem} onClick={openLoginModal}>
          <ListItemIcon className={classes.icoInList}>
            {/* <nav.icon color="primary" className={classes.ico} /> */}
            <FontAwesomeIcon icon={faSignInAlt} className={classes.faIco} />
          </ListItemIcon>
          <ListItemText primary="Login" />
        </ListItem>}

        {user.email && <ListItem button className={classes.listItem} onClick={doLogout}>
          <ListItemIcon className={classes.icoInList}>
            {/* <nav.icon color="primary" className={classes.ico} /> */}
            <FontAwesomeIcon icon={faSignOutAlt} className={classes.faIco} />
          </ListItemIcon>
          <ListItemText primary="Logout" />
        </ListItem>}

      </List>


      {/* <Divider />
      <List>
        {['All mail', 'Trash', 'Spam'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List> */}
    </div>
  )

  return (
    <div>
      <Drawer open={props.isSidebarOpen} onClose={props.closeSidebar}>
        {sideList('left')}
      </Drawer>
    </div>
  )
}

const mapStateToProps = state => ({
  isSidebarOpen: state.ui.isSidebarOpen,
  user: state.user
})

const mapDispatchToProps = dispatch => ({
  closeSidebar: () => dispatch({ type: UI_SIDEBAR_CLOSE }),
  openLoginModal: () => dispatch({type: UI_LOGIN_MODAL_OPEN}),
  resetWIPTale: () => dispatch(actionSaveTaleAndReset()),
  doLogout: () => dispatch(actionLogout())
})

export default connect(mapStateToProps, mapDispatchToProps)(Sidebard)
