export {
    startsWith,
    make3Figure
}

function startsWith (src, part) {
    return src.substring(0, part.length) === part
}

/**
 * This will make a num a 3 figure String
 * So, 9 will become 009
 * 54 will become 054
 * @param {*} num 
 */
function make3Figure (num) {
    let baseNum = 100
    let auxNum = 1000
    if (num < baseNum) {
        return (auxNum+num).toString().substring(1)
    }
    return num.toString()
}