import React, { useState } from 'react'
// import { makeStyles } from '@material-ui/core/styles'
import NextRouter, { useRouter } from 'next/router'
import { config } from '../../../utils/urls'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
// import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import { connect } from 'react-redux'
import MyLocalTales from './MyLocalTales'
// import {
//     actionCreateNewTale,
//     actionSaveTaleAndReset
// } from '../../../redux/actionCreators/createTaleActions'


const MyTales = props => {
    const [visibleTabIndex, setVisibleTab] = useState(0)

    /* FOR NeXT */
    const routeHook = useRouter()
    //////////////////////////////
    
    const handleTabSwitch = (event, newValue) => {
        setVisibleTab(newValue)
    }

    return (
        <Box p={2}>
            
            <Typography variant="h4" component="h1">
                My Tales
            </Typography>
            
            <Box mt={2} mb={4} className="flex-space-between">
                <Typography variant="subtitle1">
                    Here you will find all the tales created by you
                </Typography>
            </Box>

            <Paper square>
                <Tabs
                    value={visibleTabIndex}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleTabSwitch}
                    centered
                    aria-label="tabs to create the story"
                >
                    <Tab label="Local" />
                    <Tab label="Online" />
                </Tabs>
            </Paper>
            
            <Box className={visibleTabIndex !== 0 ? 'hide' : ''}>
                <MyLocalTales 
                    history={props.history}
                    NextRouter={NextRouter}
                />
            </Box>

            <Box className={visibleTabIndex !== 1 ? 'hide' : ''}>
                
            </Box>
        </Box>
    )
}

export default connect()(MyTales)