import { 
    Home, 
    CreateTale, 
    TalesFlow, 
    MyTales, 
    PreviewTale 
} from '../components/page-modules'

const routes = [
    {
        path: "/home",
        component: Home,
        exact: true
    },
    {
        path: "/create-tale",
        component: CreateTale,
        exact: true
    },
    {
        path: "/preview/:id",
        component: PreviewTale,
        exact: true
    },
    {
        path: "/edit-tale/:id",
        component: CreateTale,
        exact: true
    },
    {
        path: "/tales-flow",
        component: TalesFlow,
        exact: true
    },
    {
        path: "/my-tales",
        component: MyTales,
        exact: true
    }
]

export default routes