import {
    USER_UPDATE,
    USER_RESET
} from '../actionTypes'

const initState = {
    firstname: '',
    lastname: '',
    email: '',
    imgUrl: '',
    gender: '',
    pLang: '',
    sLang: '',
    country: '',
    history: '',
    readlist: ''
}

const userReducer = (state=initState, action) => {
    switch (action.type) {

        case USER_UPDATE:
            const { user } = action.payload
            return { ...state, ...user }

        case USER_RESET: 
            return {...initState}

        default:
            return state
    }
}

export default userReducer