import React, { useState, useEffect, useCallback } from 'react'
// import { makeStyles } from '@material-ui/core/styles'
import NextRouter, { useRouter } from 'next/router'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import AccountTreeIcon from '@material-ui/icons/AccountTree'
import InputIcon from '@material-ui/icons/Input'
import VisibilityIcon from '@material-ui/icons/Visibility'
import ImportTaleModal from './ImportTaleModal'
import CreateTaleIntro from './CreateTaleIntro'
import CreateTaleBody from './CreateTaleBody'
import CreateTaleFinish from './CreateTaleFinish'
import { startsWith } from '../../../utils/util'
import { config } from '../../../utils/urls'
import { connect } from 'react-redux'
import {
    actionCreateNewTale,
    actionSaveTaleAndReset,
    actionSetTaleAsWIP
} from '../../../redux/actionCreators/createTaleActions'
import { LOCAL_TALE_IDENTIFIER } from '../../../utils/constants' 

import { 
    URL_GET_STORY, 
    URL_CREATE_TALE, 
    URL_CREATE_STORYURL 
} from "../../../utils/urls"
import axios from 'axios'

function download (filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
}

const CreateTale = props => {

    /* FOR NeXT */
    const routeHook = useRouter()
    //////////////////////////////

    const [visibleTabIndex, setVisibleTab] = useState(0)
    const [pageTitle, setPageTitle] = useState("Create")
    const [isImportModalOpen, setIsImportModalOpen] = useState(false)
    const { createNewTale, saveAndResetAll, match, setTaleAsWIP, history, _id, _storyUrl, _wipTale } = props
    const [isLocalEdit, setIsLocalEdit] = useState(false)
    const [expTale, setExpTale] = useState('')
    const [isExpTaleModalVisible, setExpTModalVis] = useState(false)

    const handleTabSwitch = (event, newValue) => {
        setVisibleTab(newValue)
    }

    const goTo = (url, as) => history ? history.push(url) : NextRouter.push(as || url, url)

    const setReactaleForEditing = useCallback(() => {
        let storyUrl = routeHook ? routeHook.query.id : match.params.id
        console.log("storyUrl", storyUrl)
        console.log("routeHook", routeHook)
        console.log("match", match)



        // If it is a local story
        // Then the ID should start with "local"
        if (storyUrl.indexOf(LOCAL_TALE_IDENTIFIER) >= 0) {
            setIsLocalEdit(true)
            storyUrl = storyUrl.trim().replace(LOCAL_TALE_IDENTIFIER, "")
            const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
            const theTale = allReactales[storyUrl]
            setTaleAsWIP(theTale)
        }

        // Else fetch it online
        else {
            // If we already have the data, no need to call BE
            if(_storyUrl === storyUrl) {
                return
            }

            // else call BE
            axios.post(URL_GET_STORY, {storyUrl})
            .then(res => {
                console.log(res.data)
                let {status, tale} = res.data
                if(status === 200) {
                    tale = JSON.parse(tale)
                    setTaleAsWIP(tale)
                }
                else {
                    console.log("most probably some error occurred")
                }
            })
            .catch(err =>{
                console.log(err)
            })
        }
    }, [match, setTaleAsWIP, _storyUrl, routeHook])

    const setImportedTaleForEditing = tale => {
        setTaleAsWIP(tale)
        setIsImportModalOpen(false)
    }

    useEffect(() => {
        // if already an ID is present,
        // then already a story is in progress of create / edit
        let path = config.isNext ? routeHook.asPath : match.url

        if (path === '/create-tale' && !_id) {
            createNewTale()
        }
        // edting current in progress tale
        // coming back from FLowTale
        else if(startsWith(path, '/edit-tale/current')) {
            // Do nothing
            console.log('back from TalesFlow')
        }
        // else Edit Reactale
        else if (startsWith(path, '/edit-tale/')) {
            setPageTitle("Edit")
            setReactaleForEditing()
        }
        // Already something in progress
        // Do nothing
        else {
            console.log('Forbidden case inside create-edit... else case...')
        }

        return (() => {
            console.log("Leaving Story Create Page... Now is a good time to save changes so far...")
            //saveAndResetAll()
        })
    }, [createNewTale, saveAndResetAll, match, setReactaleForEditing, _id])

    const getWIPTale = () => {
        const { storyboard, ...tale } = _wipTale
        return tale
    }

    const exportWIPTale = () => {
        if(!areFieldsValid()) return

        const tale = getWIPTale()
        console.log(tale)
        const content = btoa(encodeURIComponent((JSON.stringify(tale))))
        download(`${tale.info.name.replace(/\s+/g, '-')}-${tale.id}.rtl`, content)
        setExpTale(content)
        setExpTModalVis(true)
    }

    const saveTaleLocal = (e, rtl) => {
        if(!areFieldsValid()) return false
        const tale = rtl || getWIPTale()
        const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
        allReactales[tale.id] = tale
        window.localStorage.setItem("allReactales", JSON.stringify(allReactales))
        alert("Saved succesfully. " + localStorageSpace())
        return true
    }

    const saveTaleOnline = async () => {
        if(!areFieldsValid()) return

        const tale = getWIPTale()

        // This variable controls whether a redirection is required to /edit-tale/
        // because of creating new unique storyUrl
        // may be because local / offline tales are being saved for the 1st time
        let needsRedirection = false
        // console.log(tale)

        // If StoryUrl has never been set
        if (!tale.info.storyUrl) {
            let res = await axios.post(URL_CREATE_STORYURL, {storyUrl: tale.info.desiredUrl || ''})
            
            tale.info.storyUrl = res.data.suggestedUrl
            needsRedirection = true
        }

        axios.post(URL_CREATE_TALE, tale)
        .then(res => {
            console.log(res.data)
            let {status, tale} = res.data
            if(status === 200) {
                alert('Tale saved online successfully...')
                tale = JSON.parse(tale)
                setTaleAsWIP(tale)

                // IF online save started from editing a local saved tale
                // Then update that local tale too, for sync
                if(isLocalEdit) {
                    saveTaleLocal(null, tale)
                }
                if (needsRedirection) {
                    history.push(`/edit-tale/${tale.info.storyUrl}`)
                }
            }
            else {
                console.log("most probably some error occurred")
            }
        })
        .catch(err =>{
            console.log(err)
        })
    }

    /**
     * If user wants to see the full tale while edting
     * he can click on preview button
     * We will save it in local and use it for the preview
     */
    const previewTale = () => {
        if(!saveTaleLocal()) return
        const tale = getWIPTale()
        goTo(`/preview/${LOCAL_TALE_IDENTIFIER}${tale.id}`, '/preview/[id]')
    }

    /**
     * It will check throught all the field validation
     * And show message + return boolean depending on it
     */
    const areFieldsValid = rtl => {
        const tale = rtl || getWIPTale()
        const { name, description, genre, lang, tags } = tale.info

        if(!name || !description || !genre || !lang || !tags) {
            alert('Fill all the mandatory fields viz. name, description, genre, language, tags')
            return false
        }
        return true
    }

    const localStorageSpace = () => {
        var allStrings = '';
        for (var key in window.localStorage) {
            if (window.localStorage.hasOwnProperty(key)) {
                allStrings += window.localStorage[key];
            }
        }
        return allStrings ? 3 + ((allStrings.length * 16) / (8 * 1024)) + ' KB' : 'Empty (0 KB)';
    };

    return (
        <Box py={2}>
            
            <Typography variant="h4" component="h1">
                {pageTitle} Reactale
            </Typography>
            
            <Box mt={2} mb={4} className="flex-space-between">
                <Typography variant="subtitle1">
                    {pageTitle} a Reactale or import one to edit it.
                </Typography>
                
                <Box>
                    <Button 
                        variant="contained" 
                        color="primary"
                        className="margin-right-15 margin-top-15"
                        startIcon={<InputIcon />}
                        onClick={() => setIsImportModalOpen(true)}
                    >
                        Import
                    </Button>
                    <Button 
                        variant="contained" 
                        color="primary"
                        className="margin-right-15 margin-top-15"
                        startIcon={<VisibilityIcon />}
                        onClick={previewTale}
                    >
                        Preview
                    </Button>
                    <Button 
                        variant="contained" 
                        color="primary"
                        className="margin-top-15"
                        startIcon={<span className="talesflowicon"><AccountTreeIcon /></span>}
                        onClick={() => goTo('/tales-flow')}
                    >
                        TalesFlow
                    </Button>
                </Box>
                
            </Box>

            <Paper square>
                <Tabs
                    value={visibleTabIndex}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleTabSwitch}
                    centered
                    aria-label="tabs to create the story"
                >
                    <Tab label="Intro" />
                    <Tab label="Body" />
                    <Tab label="Finish" />
                </Tabs>
            </Paper>
            
            <Box className={visibleTabIndex !== 0 ? 'hide' : ''}>
                <CreateTaleIntro />
            </Box>

            <Box className={visibleTabIndex !== 1 ? 'hide' : ''}>
                <CreateTaleBody />
            </Box>

            <Box className={visibleTabIndex !== 2 ? 'hide' : ''}>
                <CreateTaleFinish 
                    exportWIPTale={exportWIPTale}
                    saveTaleOnline={saveTaleOnline}
                    saveTaleLocal={saveTaleLocal}
                    expTale={expTale}
                    isExpTaleModalVisible={isExpTaleModalVisible}
                    setExpTModalVis={setExpTModalVis}
                />
            </Box>

            {/* Import Tale Modal */}
            <ImportTaleModal 
                isOpen={isImportModalOpen} 
                handleClose = {() => setIsImportModalOpen(false)}
                setForEditing = {setImportedTaleForEditing}
            />
        </Box>
    )
}
const mapStateToProps = state => ({
    _id: state.wipTale.id,
    _storyUrl: state.wipTale.info.storyUrl,
    _wipTale: state.wipTale
})
const mapDispatchToProps = dispatch => ({
    createNewTale: () => dispatch(actionCreateNewTale()),
    saveAndResetAll: () => dispatch(actionSaveTaleAndReset()),
    setTaleAsWIP: tale => dispatch(actionSetTaleAsWIP(tale))
})
export default connect(mapStateToProps, mapDispatchToProps)(CreateTale)