import React, { useState, useEffect } from 'react'
import SimpleModal from '../../common/modal/SimpleModal'
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import {
    UI_LOGIN_MODAL_CLOSE, 
    UI_LOGIN_SET_ACT_TAB
} from '../../../redux/actionTypes'
import {
    actionLogin,
    actionSignup
} from '../../../redux/actionCreators/userActions'

const useStyles = makeStyles(theme => ({
    menuButton: {
      marginRight: theme.spacing(2),
    },
    loginModal: {
        maxWidth: 600,
        padding: 0
    }
}))

const LoginModal = props => {
    const classes = useStyles()
    const {isOpen, handleClose, visibleTabIndex, title, changeTab, user, doLogin, doSignup} = props
    const handleTabSwitch = (e, tabIndex) => changeTab(tabIndex)
    const [localUser, setLocalUser] = useState(user || {})

    useEffect(() => {
        console.log("Inside useEffect, user changed")
        if(user) {
            setLocalUser(user)
        }
    }, [user])

    const handleLogin = () => {
        const { email, pswd } = localUser
        if (!email || !pswd) {
            alert('Enter Email and Password to login...')
            return
        }
        doLogin({ email, pswd })
    }

    const handleSignup = () => {
        const { email, pswd, firstname, lastname } = localUser
        if (!email || !pswd || !firstname || !lastname) {
            alert('Enter Firstname, Lastname Email, Password to login...')
            return
        }
        doSignup({ email, pswd, firstname, lastname })
    }

    const updateLocalUser = e => {
        setLocalUser({
            ...localUser,
            [e.target.name]: e.target.value
        })
    }

    return (
        <SimpleModal 
            isOpen={isOpen} 
            handleClose = {handleClose}
            title={title || "Hi There!"}
            className={classes.loginModal}
        >
            <Paper square>
                <Tabs
                    value={visibleTabIndex}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleTabSwitch}
                    centered
                    aria-label="tabs to create the story"
                >
                    <Tab label="Sign in" />
                    <Tab label="Sign up" />
                    {/* <Tab label="Recovery" /> */}
                </Tabs>
            </Paper>
            
            {/* Sign In Section */}
            <Box className={visibleTabIndex !== 0 ? 'hide' : ''}>
                <TextField label="Email" name="email" value={localUser.email} onChange={updateLocalUser} variant="outlined" size="small" fullWidth className="margin-top-25" />
                <TextField label="Password" name="pswd" value={localUser.pswd} onChange={updateLocalUser} type="password" variant="outlined" size="small" fullWidth className="margin-top-25" />
                <Button 
                    variant="outlined" 
                    color="primary"
                    className="margin-top-25"
                    fullWidth
                    onClick={handleLogin}
                >
                    Sign in
                </Button>
            </Box>

            {/* Sign Up Section */}
            <Box className={visibleTabIndex !== 1 ? 'hide' : ''}>
                <TextField label="First name" name="firstname" value={localUser.firstname} onChange={updateLocalUser} variant="outlined" size="small" fullWidth className="margin-top-25" />
                <TextField label="Last name" name="lastname" value={localUser.lastname} onChange={updateLocalUser} variant="outlined" size="small" fullWidth className="margin-top-25" />
                <TextField label="Email" name="email" value={localUser.email} onChange={updateLocalUser} variant="outlined" size="small" fullWidth className="margin-top-25" />
                <TextField label="Password" name="pswd" value={localUser.pswd} onChange={updateLocalUser} type="password" variant="outlined" size="small" fullWidth className="margin-top-25" />
                <Button 
                    variant="outlined" 
                    color="primary"
                    className="margin-top-25"
                    fullWidth
                    onClick={handleSignup}
                >
                    Sign up
                </Button>
            </Box>

            {/* Password Recovery Section */}
            {/* <Box className={visibleTabIndex !== 2 ? 'hide' : ''}>
                Recovery
            </Box> */}
            

            {/* <Box align="center" my={1}>
                <Typography variant="h6" gutterBottom>
                    <b>OR</b>
                </Typography>
            </Box> */}

            
        </SimpleModal>
    )
}

const mapStateToProps = state => ({
    isOpen: state.ui.isLoginModalOpen,
    visibleTabIndex: state.ui.loginActiveTab,
    user: state.user
})

const mapDispatchToProps = dispatch => ({
    handleClose: () => dispatch({type: UI_LOGIN_MODAL_CLOSE}),
    changeTab: tabIndex => dispatch({type: UI_LOGIN_SET_ACT_TAB, payload: {tabIndex}}),
    doLogin: u => dispatch(actionLogin(u)),
    doSignup: u => dispatch(actionSignup(u))
})

export default connect(mapStateToProps, mapDispatchToProps)(LoginModal)