import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
// import Paper from '@material-ui/core/Paper'
// import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import ExportTaleModal from './ExportTaleModal'
import { InfoCard } from '../../common/cards'
import featureText from './exportFeatureText'
import { Li } from '../../common/misc'
import axios from 'axios'
import {
    actionSetTaleAsWIP
} from '../../../redux/actionCreators/createTaleActions'

const useStyles = makeStyles(theme => ({
    cardHeaders: {
        fontWeight: 500,
        margin: 0,
        textAlign: 'center',
        color: theme.palette.primary.main
    },
    cardContainer: {
        marginTop: theme.spacing(3)
    },
    cardInfo: {
        minHeight: 50,
        marginTop: 3
    }
}))

const CreateTaleFinish = props => {
    const classes = useStyles()
    const { exportWIPTale, saveTaleOnline, saveTaleLocal, expTale, isExpTaleModalVisible, setExpTModalVis } = props
    
    return (
        <Box pb={10}>
            <ExportTaleModal 
                isOpen = {isExpTaleModalVisible}
                handleClose = {() => setExpTModalVis(false)}
                expTaleContent = {expTale}
            />
            <Container maxWidth="md">
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={4} className={classes.cardContainer}>
                        <InfoCard>
                            <h2 className={classes.cardHeaders}>Export Reactale</h2>
                            <span className={classes.cardInfo}>{featureText.exportTale.desc}</span>

                            <h3 className={classes.cardHeaders}>Pros &amp; Cons</h3>
                            <ul className="fillmid">
                                {
                                    featureText.exportTale.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={exportWIPTale}
                            >
                                Export Reactale
                            </Button>
                        </InfoCard>
                    </Grid>
                    <Grid item xs={12} sm={4} className={classes.cardContainer}>
                        <InfoCard>
                            <h2 className={classes.cardHeaders}>Save Offline</h2>
                            <span className={classes.cardInfo}>{featureText.saveLocal.desc}</span>

                            <h3 className={classes.cardHeaders}>Pros &amp; Cons</h3>
                            <ul className="fillmid">
                                {
                                    featureText.saveLocal.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={saveTaleLocal}
                            >
                                Save Offline
                            </Button>
                        </InfoCard>
                    </Grid>
                    <Grid item xs={12} sm={4} className={classes.cardContainer} >
                        <InfoCard>
                            <h2 className={classes.cardHeaders}>Save Online</h2>
                            <span className={classes.cardInfo}>{featureText.saveOnline.desc}</span>

                            <h3 className={classes.cardHeaders}>Pros &amp; Cons</h3>
                            <ul className="fillmid">
                                {
                                    featureText.saveOnline.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={saveTaleOnline}
                            >
                                Save Online
                            </Button>
                        </InfoCard>
                    </Grid>
                </Grid>
            </Container>
            {/* <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={exportWIPTale}
            >
                Export this Reactale
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={saveTaleLocal}
            >
                Save in local
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-top-15"
                onClick={saveTaleOnline}
            >
                Save Online
            </Button> */}
        </Box>
    )
}
export default CreateTaleFinish