import React, { useState } from 'react'
import { connect } from 'react-redux'
import Box from '@material-ui/core/Box';
import Storylet from '../../common/storylet/Storylet'
import FullScreenEdit from '../../common/modal/FullScreenEdit'

const CreateTaleBody = props => {
    const [fseParam, setFSEParam] = useState({})    // Full Screen Edit    
    const closeFSE = () => setFSEParam({})
    // component is either a St or a Ch object
    const openFSE = component => {
        console.log(component)
        setFSEParam({
            component, isVisible: true
        })
    }

    return (
        <Box py={3}>
            {
                props.storyboard && props.storyboard.map( (sbItem, sbI) => {
                    let st = props.storylets[sbItem.stID]
                    return (
                        <Storylet 
                            st={st} 
                            key={sbItem.stID} 
                            sbI={sbI} 
                            sbItem={sbItem} 
                            openFSE={openFSE}
                        />
                    )
                })
            }
            <FullScreenEdit 
                isOpen={fseParam.isVisible}
                handleClose={closeFSE}
                component={fseParam.component}
            />
        </Box>
    )
}

const mapStateToProps = state => ({
    storyboard: state.wipTale.storyboard,
    storylets: state.wipTale.storylets
})

export default connect(mapStateToProps)(CreateTaleBody)