import { URL_LOGIN, URL_SIGNUP, URL_LOGOUT } from '../../utils/urls'
import { USER_UPDATE, USER_RESET, UI_LOGIN_MODAL_CLOSE } from '../actionTypes'
import axios from 'axios'

export const actionLogin = user => {
    const { email, pswd } = user
    return dispatch => {
        axios.post(URL_LOGIN, { email, pswd })
            .then(res => {
                let r = res.data
                if (r.status === 200) {
                    let user = r.data

                    // update the user in redux store
                    dispatch({
                        type: USER_UPDATE,
                        payload: { user }
                    })

                    // Close the popup
                    dispatch({ type: UI_LOGIN_MODAL_CLOSE })

                    alert("Sign in successfull...")
                }
                else {
                    alert('Sign in failed...')
                }
            })
            .catch(err => {
                console.log(err)
                alert('Sign in failed...')
            })
    }
}

export const actionSignup = user => {
    const { email, pswd, firstname, lastname } = user
    return dispatch => {
        axios.post(URL_SIGNUP, { email, pswd, firstname, lastname })
            .then(res => {
                let r = res.data
                if (r.status === 200 || r.status === 1152) {
                    let user = r.data

                    //Update user in redux
                    dispatch({
                        type: USER_UPDATE,
                        payload: { user }
                    })

                    // Close the popup
                    dispatch({ type: UI_LOGIN_MODAL_CLOSE })

                    alert("Sign up successfull...")
                }
                else {
                    alert('Sign up failed...')
                }
            })
            .catch(err => {
                console.log(err)
                alert('Sign up failed...')
            })
    }
}

export const actionLogout = user => {
    return dispatch => {
        axios.get(URL_LOGOUT)
            .then(res => {
                let r = res.data
                if (r.status === 200) {
                    dispatch({
                        type: USER_RESET
                    })

                    alert("Succesfully signed out...")
                }
                else {
                    alert('Log out failed...')
                }
            })
            .catch(err => {
                console.log(err)
                alert('Log out failed...')
            })
    }
}