import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
// import Paper from '@material-ui/core/Paper'
import InputAdornment from '@material-ui/core/InputAdornment'
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'
import Select from '@material-ui/core/Select'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import Tooltip from '@material-ui/core/Tooltip'
import InfoIcon from '@material-ui/icons/Info'
import MenuItem from '@material-ui/core/MenuItem'
import { connect } from 'react-redux'
import {
    TALE_UPDATE_INFO
} from '../../../redux/actionTypes'
import { 
    COUNTRIES,
    LANGUAGES,
    GENRES 
} from '../../../utils/constants'
// const useStyles = makeStyles(theme => ({
//     root: {
//     }
// }))

const updateTaleInfo = (dispatch, key, val, oldInfo) => {
    dispatch({
        type: TALE_UPDATE_INFO,
        payload: {
            ...oldInfo,
            [key]: val
        }
    })
}

const CreateTaleIntro = props => {
    // const classes = useStyles()
    const { info, typingVal, user } = props

    return (
        <Box py={3}>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={6} md={3}>
                    <TextField
                        fullWidth
                        label="Name of Tale"
                        variant="outlined"
                        value={info.name}
                        onChange={e => typingVal('name', e.target.value, info)}
                        InputProps={info.name.length <= 0 && {
                            endAdornment: <InputAdornment position="end"><ErrorOutlineIcon /></InputAdornment>,
                        }}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    {/* <TextField
                        fullWidth
                        label="Genre"
                        variant="outlined"
                        value={info.genre}
                        onChange={e => typingVal('genre', e.target.value, info)}
                    /> */}
                    <FormControl variant="outlined" fullWidth>
                        <InputLabel id="demo-simple-select-outlined-label">
                            Genre
                        </InputLabel>
                        <Select
                            labelId="demo-simple-select-outlined-label"
                            value={info.genre}
                            fullWidth
                            onChange={e => typingVal('genre', e.target.value, info)}
                        >
                            {
                                GENRES.map(g => (
                                    <MenuItem value={g.toUpperCase()} key={g}>
                                        {g}
                                    </MenuItem>
                                ))
                            }
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <TextField
                        fullWidth
                        label="Tags"
                        variant="outlined"
                        value={info.tags}
                        onChange={e => typingVal('tags', e.target.value, info)}
                    />
                </Grid>
                {/* <Grid item xs={12} sm={6} md={3}>
                    <TextField
                        fullWidth
                        label="Language"
                        variant="outlined"
                        value={info.lang}
                        onChange={e => typingVal('lang', e.target.value, info)}
                        InputProps={info.lang.length <= 0 && {
                            endAdornment: <InputAdornment position="end"><ErrorOutlineIcon /></InputAdornment>,
                        }}
                    />
                </Grid> */}
                <Grid item xs={12} sm={6} md={3}>
                    <FormControl variant="outlined" fullWidth>
                        <InputLabel id="demo-simple-select-outlined-label">
                            Language
                        </InputLabel>
                        <Select
                            labelId="demo-simple-select-outlined-label"
                            value={info.lang}
                            fullWidth
                            onChange={e => typingVal('lang', e.target.value, info)}
                        >
                            {
                                LANGUAGES.map(l => (
                                    <MenuItem value={l.toUpperCase()} key={l}>
                                        {l}
                                    </MenuItem>
                                ))
                            }
                        </Select>
                    </FormControl>
                </Grid>
                {/* <Grid item xs={12} sm={6} md={3}>
                    <TextField 
                        fullWidth 
                        label="Email" 
                        variant="outlined" 
                        value={user.email} 
                        disabled
                        // onChange={e => typingVal('authorEmail', e.target.value, info)}
                    />
                </Grid> */}
                <Grid item xs={12} sm={6} md={3}>
                    <TextField
                        fullWidth
                        label="Original Author (if any)"
                        variant="outlined"
                        placeholder="Original creator's name"
                        value={info.originalAuthor}
                        onChange={e => typingVal('originalAuthor', e.target.value, info)}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    {!info.storyUrl &&
                    <TextField
                        fullWidth
                        label="Custom URL"
                        variant="outlined"
                        value={info.desiredUrl}
                        onChange={e => typingVal('desiredUrl', e.target.value, info)}
                    />}
                    {info.storyUrl &&
                        <TextField
                            fullWidth
                            label="Fixed URL"
                            variant="outlined"
                            disabled
                            value={'/' + info.storyUrl}
                        />
                    }
                    {/* <Tooltip
                        PopperProps={{
                            disablePortal: true,
                        }}
                        onClose={() => {}}
                        open={true}
                        disableFocusListener
                        disableHoverListener
                        disableTouchListener
                        title="Add"
                        placement="left"
                    >
                        <InfoIcon />
                    </Tooltip> */}
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        label="Brief description"
                        multiline
                        rows="4"
                        fullWidth
                        variant="outlined"
                        value={info.description}
                        onChange={e => typingVal('description', e.target.value, info)}
                        InputProps={info.description.length <= 100 && {
                            endAdornment: <InputAdornment position="end"><ErrorOutlineIcon /></InputAdornment>,
                        }}
                    />
                </Grid>
            </Grid>
        </Box>
    )
}

const mapStateToProps = state => ({
    info: state.wipTale.info,
    user: state.user
})
const mapDispatchToProps = dispatch => ({
    typingVal: (key, val, oldInfo) => updateTaleInfo(dispatch, key, val, oldInfo)
})

export default connect(mapStateToProps, mapDispatchToProps)(CreateTaleIntro)