import React, { useEffect, useState } from 'react'
import { URL_ALLTALES } from '../../../utils/urls'
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import axios from 'axios'
import { ComplexCard } from '../../common/cards'
import { config } from '../../../utils/urls'

const Home = props => {
    const [tales, setTales] = useState([])
    const { history } = props
    useEffect(() => {
        axios.get(URL_ALLTALES)
        .then(res => {
            console.log(res.data)
            setTales(res.data.tales)
        })
    }, [])

    const goToTale = storyUrl => {
        // if it an App, go to preview url i.e. SPA way
        if(config.isApp) {
            history.push(`/preview/${storyUrl}`)
        }
        // else go to Real PHP View
        else {

        }
    }

    return (
        <Box py={5}>
            {
                tales.length === 0 &&
                <h2 className="margin-top-25">
                    Oops! looks like an issue. We could not fetch any tales at the moment. :(
                </h2>
            }
            <Grid container spacing={2}>
            {
                tales.length > 0 && tales.map (t => 
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={2} key={t.storyUrl}>
                        <ComplexCard 
                            id={t.storyUrl}
                            info={t} 
                            expandable={false} 
                            pageID='HOME'
                            handleReadTale={() => goToTale(t.storyUrl)}
                            // handleEdit={goToEditTale}
                            // handleDelete={deleteTale}  
                        />
                    </Grid>
                )
            }
            </Grid>
        </Box>
    )
}

export default Home