import React, { createRef } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { connect } from 'react-redux'
import TextField from '@material-ui/core/TextField'
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import SimpleModal from './SimpleModal'
import { 
    actionUpdateSTAllText,
    actionUpdateCText
} 
from '../../../redux/actionCreators/createTaleActions'

const useStyles = makeStyles(theme => ({
    fullScreen: {
        height: "100%",
        width: "100%",
        overflow: 'auto'
    },
    textField: {
        marginTop: 20,
        '& textarea': {
            minHeight: 'calc(100vh - 250px)'
        }
    },
    btnPanel: {
        display: 'flex',
        justifyContent: "flex-end",
        marginTop: 20,
        '& button+button': {
            marginLeft: 15
        }
    }
  }))

const FullScreenEdit = props => {
    const classes = useStyles()
    const { component, handleClose, saveStValue, saveChValue } = props
    const t = component && component.id[0]==='S' ? 'Storylet' : 'Choice'
    const theForm = createRef()
    const save = () => {
        const compText = theForm.current.elements.compText.value
        if (t === 'Storylet') {
            const compTitle = theForm.current.elements.compTitle.value
            saveStValue(component.id, compTitle, compText)
        }
        else if (t === 'Choice') {
            saveChValue(component.id, compText)
        }
        handleClose()
    }

    return (
        <SimpleModal
            title={"Edit " + t}
            {...props} 
            className={classes.fullScreen}
            preventAutoSize={true}
        >
            
            <Box>
                <form noValidate autoComplete="off" ref={theForm}>
                    {(t === 'Storylet') && <TextField
                        label="Storylet title"
                        variant="outlined"
                        defaultValue={component && component.title}
                        fullWidth
                        name="compTitle"
                    />}
                    <TextField
                        label={t + " body"}
                        multiline
                        variant="outlined"
                        defaultValue={component && component.text}
                        fullWidth
                        className={classes.textField}
                        name="compText"
                    />
                </form>

                <Box className={classes.btnPanel}>
                    <Button variant="outlined" color="primary" onClick={handleClose}>Cancel</Button>
                    <Button variant="contained" color="primary" onClick={save}>Done</Button>
                </Box>
            </Box>

        </SimpleModal>
    )
}

const mapDispatchToProps = dispatch => ({
    saveStValue: (stID, title, text) => dispatch(actionUpdateSTAllText(stID, title, text)),
    saveChValue: (cID, value) => dispatch(actionUpdateCText(cID, value))
})

export default connect(null, mapDispatchToProps)(FullScreenEdit)