import React from 'react'
import { Link as FLink } from 'react-router-dom'
import BLink from 'next/link'
import { config } from '../../utils/urls'

const ULink = props => {
    const { to, href, as } = props
    const l = to || href
    return (
        config.isNext ? <BLink href={l} as={as || l} {...props} /> : <FLink to={l} {...props} />
    )
}

export default ULink

