import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
// import logoImg from '../../../assets/img/logo/Reactale-logo-50h-white-bg.png'
import styles from './style.module.css'
import { connect } from 'react-redux'
import { UI_SIDEBAR_OPEN } from '../../../redux/actionTypes'

const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  toolbar: {
    padding: '0 10px'
  },
  greet: {
    width: 75,
    fontSize: 14,
    textAlign: 'center',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    marginLeft: 7
  }
}))

const Header = props => {
  const classes = useStyles()
  const { openSidebar, user } = props
  // console.log(styles)
  return (
    <div className="flex-grow-1">
      <AppBar position="static">
        <Toolbar className={classes.toolbar}>
          <IconButton edge="start" onClick={openSidebar} className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <div className={styles.logoContainer}>
            <img src="/static/img/logo/Reactale-logo-50h-white-bg.png" alt="Reactale Logo" className={styles.logoImg} />
          </div>          
          {/* <Button color="inherit" onClick={openLoginModal}>Login</Button> */}
          <div className={classes.greet}>
            {!user.firstname && <span title="Welcome Guest">Welcome Guest!</span>}
            {user.firstname && <span title={'Welcome ' + user.firstname}>Welcome {user.firstname}!</span>}
          </div>
        </Toolbar>
      </AppBar>
    </div>
  )
}

const mapStateToProps = state => ({
  user: state.user
})
const mapDispatchToProps = dispatch => ({
  openSidebar: () => dispatch({type: UI_SIDEBAR_OPEN})
})

export default connect(mapStateToProps, mapDispatchToProps)(Header)
